@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Aldrich&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #174ab1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: "Aldrich", sans-serif !important;
}
/****************************************** NAVBAR ********************************************/

nav {
  background: linear-gradient(90deg, #a9643e 50%, #161616 50%);
  z-index: 2;
}

.logo {
  width: 4.5rem;
  margin-left: 8.55rem;
  margin-top:  1rem;
}

.navbar-collapse {
  margin-right: 7.2rem;
  text-align: right;
}
.navbar-toggler {
  margin-right: 1.5rem;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: 20px;
  letter-spacing: 0.03rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 1.2rem;
  margin-left: 1.2rem;
  color: #ffffff !important;
  text-align: right;
  margin-top:  1.3rem;
}

.navbar-brand {
  max-width: 20rem;
  padding: 0 !important;
  height: 5rem !important;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #a9643e;
}
.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #a9643e;
}

@media (min-width: 1281px) and (max-height: 1920px) {
  .logo {
    width: 4.5rem;
    margin-left: 8.55rem;
    margin-top:  1rem;
  }

  .navbar-collapse {
    margin-right: 7.2rem;
    text-align: right;
  }
}

@media (min-width: 1025px) and (max-width: 1280px) {
  .logo {
    width: 4.5rem;
    margin-left: 3.5rem;
    margin-top:  1rem;
  }
  .navbar-collapse {
    margin-right: 2.2rem;
    text-align: right;
  }
}

@media (max-width: 1024px) {
  .logo {
    width: 4.5rem;
    margin-left: 2.35rem;
  }
  .navbar-collapse {
    margin-right: 2rem;
    text-align: right;
  }
    .navbar-nav > li > a {
    margin-right: 0.5rem;
    margin-left: 0.5rem;
    color: #ffffff !important;
    text-align: right;
    margin-top:  1.3rem;
  }
}

@media (width: 768px) {
  .logo {
    width: 4.5rem;
    margin-left: 1.8rem;
  }
  .navbar-toggler {
  margin-right: 1.5rem;
