.header-wraper h1 {
  font-size: 5.8rem;
  text-transform: uppercase;
  letter-spacing: 30px;
  font-weight: 700;
  color: #ffffff;
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  mix-blend-mode: difference;
  z-index: 9;
}

.banner {
  display: flex;
  background: linear-gradient(90deg, #a9643e 50%, #161616 50%);
  position: relative;
  height: 105vh;
  margin: 0;
}

.homeMain {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-text {
  font-size: 4rem;
  font-family: "Krona One", sans-serif !important;
  text-transform: uppercase;
  letter-spacing: 0px;
  font-weight: 900;
  color: #ffffff;
  position: absolute;
  display: block;
  width: 90%;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  padding-left: 35px;
  padding-top: 20px;
}

.mainpic {
  width: 750px;
  margin: auto auto;
  display: block;
}

.scroll {
  font-weight: 700;
  font-size: 0.875rem;
  left: 155px;
  letter-spacing: 1.5px;
  color: #ffffff;
  text-transform: uppercase;
  position: absolute;
  bottom: 48px;
}
.fixed-misc {
  position: fixed;
  top: 50%;
  right: -100px;
  transform: rotate(-90deg);
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.875rem;
  letter-spacing: 1.5px;
  color: #ffffff;
  z-index: 8;
}

